import { createApi, fakeBaseQuery } from "@reduxjs/toolkit/query/react";
import { User, Inventory } from "../types";
import { data } from  "./mockData";
import { status } from "nprogress";

export const baseApi = createApi({
  reducerPath: 'baseApi',
  baseQuery: fakeBaseQuery(),
  tagTypes: ["User"],
  endpoints: (builder) => ({
    // getUsers: builder.query<User[], void>({
    //   async queryFn() {
    //     try {
    //       let users: User[] = [];
    //       data.users.forEach((user) => {
    //         users.push(user as User);
    //       });
    //       return { data: users };
    //     } catch (error: any) {
    //       console.error(error.message);
    //       return { error: error.message };
    //     }
    //   },
    // }),
    getUsersMe: builder.query<User, void>({
      async queryFn() {
        try {
          let user: User = data.users[0];
          return { data: user };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
    getInventoriesFromUser: builder.query<Inventory[], ParamUserId>({
      async queryFn(queryArg) {
        try {
          let inventories: Inventory[] = data.inventories.filter(x => x.by_user == queryArg.userId) as Inventory[];
          return { data: inventories };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
    getInventoryById: builder.query<Inventory, ParamInventoryId>({
      async queryFn(queryArg) {
        try {
          let inventory: Inventory = data.inventories.find(x => x.id == queryArg.inventoryId) as Inventory;
          return { data: inventory };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
    deleteInventoryById: builder.mutation<PostResponse,ParamInventoryId>({
      async queryFn({ inventoryId }) {
        try {
          return { data: null };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
  }),
});

export type ParamUserId = {
  userId: number;
};

export type ParamInventoryId = {
  inventoryId: number;
};

export type PostResponse = {
  status: boolean,
  message: string,
}

export const { 
  useGetUsersMeQuery,
  useGetInventoriesFromUserQuery,
  useGetInventoryByIdQuery,
  useDeleteInventoryByIdMutation,
} = baseApi;