import { Suspense, lazy } from 'react';
import { Navigate } from 'react-router-dom';
import { RouteObject } from 'react-router';

import SidebarLayout from 'src/layouts/SidebarLayout';
import InventorySidebarLayout from 'src/layouts/InventorySidebarLayout';
import BaseLayout from 'src/layouts/BaseLayout';

import SuspenseLoader from 'src/components/SuspenseLoader';
import { useSelector } from 'react-redux';
import { RootState } from './store';

const Loader = (Component) => (props) =>
  (
    <Suspense fallback={<SuspenseLoader />}>
      <Component {...props} />
    </Suspense>
  );

// Pages

const Home = Loader(lazy(() => import('src/content/home')));

// Applications

const CustomersList = Loader(
  lazy(() => import('src/content/applications/inventories/List'))
);
const InventoryDelete = Loader(
  lazy(() => import('src/content/applications/inventory/Delete'))
);
const InventoryHome = Loader(
  lazy(() => import('src/content/applications/inventory/Home'))
);
// const DataCustomerAndInventoried = Loader(
//   lazy(() => import('src/content/applications/inventory/DataCustomerAndInventoried'))
// );
const UserSignup = Loader(
  lazy(() => import('src/content/applications/user/SignUp'))
);
const UserSignIn = Loader(
  lazy(() => import('src/content/applications/user/SignIn'))
);

// Status

const Status404 = Loader(
  lazy(() => import('src/content/pages/Status/Status404'))
);
const Status500 = Loader(
  lazy(() => import('src/content/pages/Status/Status500'))
);
const StatusComingSoon = Loader(
  lazy(() => import('src/content/pages/Status/ComingSoon'))
);
const StatusMaintenance = Loader(
  lazy(() => import('src/content/pages/Status/Maintenance'))
);

// Auth to Private Routes

const PrivateRoute = ({ children }: any) => {
  const token = useSelector((state: RootState) => state.auth.token);
  return token ? children : <Navigate to="/login" replace />;
};

const RedirectIfAuthorized = ({ children }: any) => {
  const token = useSelector((state: RootState) => state.auth.token);
  return token ? <Navigate to="/inventories/list" replace /> : children;
};

const routes: RouteObject[] = [
  {
    path: '/',
    element: <BaseLayout />,
    children: [
      {
        path: '/',
        element: (
          <RedirectIfAuthorized>
            <Home />
          </RedirectIfAuthorized>
        )
      },
      {
        path: 'home',
        element: <Navigate to="/" replace />
      },
      {
        path: 'status',
        children: [
          {
            path: '/status/',
            element: <Navigate to="404" replace />
          },
          {
            path: '404',
            element: <Status404 />
          },
          {
            path: '500',
            element: <Status500 />
          },
          {
            path: 'maintenance',
            element: <StatusMaintenance />
          },
          {
            path: 'coming-soon',
            element: <StatusComingSoon />
          }
        ]
      },
      {
        path: '*',
        element: <Status404 />
      }
    ]
  },
  {
    path: '/inventories',
    element: <SidebarLayout />,
    children: [
      {
        path: '/inventories/',
        element: (
          <PrivateRoute>
            <Navigate to="/inventories/list" replace />
          </PrivateRoute>
        )
      },
      {
        path: 'list',
        element: (
          <PrivateRoute>
            <CustomersList />
          </PrivateRoute>
        )
      },
    ]
  },
  {
    path: '/inventory',
    element: <InventorySidebarLayout />,
    children: [
      {
        path: '/inventory/',
        element: (
          <PrivateRoute>
            <Navigate to="/inventory/home" replace />
          </PrivateRoute>
        )
      },
      {
        path: 'delete/:id',
        element: (
          <PrivateRoute>
            <InventoryDelete />
          </PrivateRoute>
        )
      },
      {
        path: 'home/:id',
        element: (
          <PrivateRoute>
            <InventoryHome />
          </PrivateRoute>
        )
      },
      // {
      //   path: 'customer',
      //   element: (
      //     <PrivateRoute>
      //       <DataCustomerAndInventoried />
      //     </PrivateRoute>
      //   )
      // },
    ]
  },
  {
    path: 'register',
    element: (
      <RedirectIfAuthorized>
        <UserSignup />
      </RedirectIfAuthorized>
    )
  },
  {
    path: 'login',
    element: (
      <RedirectIfAuthorized>
        <UserSignIn />
      </RedirectIfAuthorized>
    )
  }
];

export default routes;
