import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../store';

const slice = createSlice({
  name: 'auth',
  initialState: { username: null, token: null } as {
    username: null | string;
    token: null | string;
  },
  reducers: {
    setCredentials: (
      state,
      {
        payload: { username, token }
      }: PayloadAction<{ username: string; token: string }>
    ) => {
      state.username = username;
      state.token = token;
    },
    clearCredentials: (state) => {
      state.username = null;
      state.token = null;
    }
  },
  extraReducers: (builder) => {}
});

export const { setCredentials, clearCredentials } = slice.actions;
export default slice.reducer;
export const selectCurrentUser = (state: RootState) => state.auth.username;
