import { configureStore } from '@reduxjs/toolkit';
import { baseApi } from './services/baseApi';
// import { drHerancaApi } from './services/drHerancaApi';
// import { drHerancaApi as drHerancaApiFormData } from './services/drHerancaApiFormData';
// import { drHerancaApi as drHerancaApiStrapi } from './services/drHerancaApiStrapi';
import auth from 'src/features/auth/authSlice';
import downloadFileReducer from 'src/features/download/downloadFileSlice';
import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';

import storage from 'redux-persist/lib/storage';
import { combineReducers } from 'redux';
import {
  FLUSH,
  PAUSE,
  PERSIST,
  persistReducer,
  persistStore,
  PURGE,
  REGISTER,
  REHYDRATE
} from 'redux-persist';

const rootReducer = combineReducers({
  [baseApi.reducerPath]: baseApi.reducer,
  // [drHerancaApi.reducerPath]: drHerancaApi.reducer,
  // [drHerancaApiFormData.reducerPath]: drHerancaApiFormData.reducer,
  // [drHerancaApiStrapi.reducerPath]: drHerancaApiStrapi.reducer,
  auth,
  dashReducer: downloadFileReducer
});

const persistConfig = {
  key: 'root',
  version: 1,
  storage,
  whitelist: ['auth']
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

export const store = configureStore({
  reducer: persistedReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      thunk: {
        extraArgument: downloadFileReducer
      },
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER]
      }
    }).concat(baseApi.middleware),
});

export const persistor = persistStore(store);
export type AppDispatch = typeof store.dispatch;
export const useAppDispatch = () => useDispatch<AppDispatch>();
export type RootState = ReturnType<typeof store.getState>;
export const useTypedSelector: TypedUseSelectorHook<RootState> = useSelector;
