import { useRef, useState } from 'react';

import { NavLink } from 'react-router-dom';

import {
  Avatar,
  Box,
  Button,
  Divider,
  Hidden,
  lighten,
  List,
  ListItem,
  ListItemText,
  Popover,
  Typography,
  CircularProgress,
  Tooltip
} from '@mui/material';

import { styled } from '@mui/material/styles';
import ExpandMoreTwoToneIcon from '@mui/icons-material/ExpandMoreTwoTone';
import LockOpenTwoToneIcon from '@mui/icons-material/LockOpenTwoTone';
import DashboardIcon from '@mui/icons-material/Dashboard';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { clearCredentials } from '../../../../features/auth/authSlice';

import {
  useGetUsersMeQuery
} from 'src/services/baseApi';

const UserBoxButton = styled(Button)(
  ({ theme }) => `
        padding-left: ${theme.spacing(1)};
        padding-right: ${theme.spacing(1)};
`
);

const MenuUserBox = styled(Box)(
  ({ theme }) => `
        background: ${theme.colors.alpha.black[5]};
        padding: ${theme.spacing(2)};
`
);

const UserBoxText = styled(Box)(
  ({ theme }) => `
        text-align: left;
        padding-left: ${theme.spacing(1)};
`
);

const UserBoxLabel = styled(Typography)(
  ({ theme }) => `
        font-weight: ${theme.typography.fontWeightBold};
        color: ${theme.palette.secondary.main};
        display: block;
`
);

const UserBoxDescription = styled(Typography)(
  ({ theme }) => `
        color: ${lighten(theme.palette.secondary.main, 0.5)}
`
);

function HeaderUserbox() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { data, isLoading } = useGetUsersMeQuery(
    null,
    {
      refetchOnMountOrArgChange: true
    }
  );

  const ref = useRef<any>(null);
  const [isOpen, setOpen] = useState<boolean>(false);

  const handleOpen = (): void => {
    setOpen(true);
  };

  const handleClose = (): void => {
    setOpen(false);
  };

  const logout = (): void => {
    dispatch(clearCredentials());
  };

  const BooksCreateAvatar = (props) => {
    if (isLoading) return (<></>)
    return (
      <Avatar variant="rounded" alt={data.name}>
        {data.name.substring(0, 1)}
      </Avatar>
    );
  };

  const BooksCreateAvatarSummary = (props) => {
    if (isLoading) return (<></>)
      return (
      <UserBoxText>
        <UserBoxLabel variant="body1">{data.name}</UserBoxLabel>
        <UserBoxDescription variant="body2">
          {data.email}
        </UserBoxDescription>
      </UserBoxText>
    );
  };

  return (
    <>
      <UserBoxButton color="secondary" ref={ref} onClick={handleOpen}>
        <BooksCreateAvatar />
        <Hidden mdDown>
          <BooksCreateAvatarSummary />
        </Hidden>
        <Hidden smDown>
          <ExpandMoreTwoToneIcon sx={{ ml: 1 }} />
        </Hidden>
      </UserBoxButton>
      <Popover
        anchorEl={ref.current}
        onClose={handleClose}
        open={isOpen}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}
      >
        <MenuUserBox sx={{ minWidth: 210 }} display="flex">
          <BooksCreateAvatar />
          <BooksCreateAvatarSummary />
        </MenuUserBox>
        <Divider sx={{ mb: 0 }} />
        <List sx={{ p: 1 }} component="nav">
          <ListItem button to="/inventories/list" component={NavLink}>
            <DashboardIcon fontSize="small" />
            <ListItemText primary="Painel" />
          </ListItem>
          {/* <ListItem button to="/management/transactions" component={NavLink}>
          <ReceiptIcon fontSize="small" />
          <ListItemText primary="Consumo" />
        </ListItem> */}
          {/* <ListItem button to="/management/user" component={NavLink}>
          <AccountBoxTwoToneIcon fontSize="small" />
          <ListItemText primary="Perfil" />
        </ListItem> */}
        </List>
        <Divider />
        <Box sx={{ m: 1 }}>
          <Button color="primary" fullWidth onClick={logout}>
            <LockOpenTwoToneIcon sx={{ mr: 1 }} />
            Sair
          </Button>
        </Box>
      </Popover>
    </>
  );
}

export default HeaderUserbox;
